  import { mapMutations } from 'vuex'
  import axios from 'axios'
  export default {
    name: 'accountsLogin',
    data () {
      return {
        type: 'password',
        readonly: true,
        autofocus: true,
        autocomplete: true,
        eye: {
          open: false,
          reverse: false
        },
        inputName: '',
        inputPwd: '',
        inputPwds: '',
        inputTell: '',
        inputCode: '',
        captchaLabel:"获取验证码",
        seconds:60,
        captchaDisable:false,
        timer:null,
        isLoading: false,
        name: '',
        password: '',
        clearable: {
          visible: true,
          blurHidden: false
        },
        options: [1, 2, 3, 4, 5, 6],
        value: '',
        title: '请选择单位部门',
        placeholder: '设置密码',
        autoPop: false,
        disabled: false
      }
    },
    methods:{
      ...mapMutations(['changeLogin']),
      //显示密码
      showPwd (){
        if(this.visible === 'password'){
          this.visible = 'text'
        }else{
          this.visible = 'password'
        }
      },
      //登录
      accountsSign(){
        let nameReg = /^[a-zA-Z][a-zA-Z0-9]{3,15}$/;
        if (!this.inputName) {
          this.$message.error('请输入正确的用户名称')
          return false
        }
//        let pwdReg=/^[a-zA-Z0-9]{4,10}$/;
        if (!this.inputPwd) {
          this.$message.error('请输入密码');
          return false
        }
        this.subAccountsSign()
      },
      subAccountsSign(){
        let _this = this
        this.fullscreenLoading = true;
        let params = new URLSearchParams();
        params.append('loginName',this.inputName );
        params.append('pwd', this.inputPwd);
        axios({
          method: 'post',
          url: '/Home/Person/login',
          data: params
        }).then(function (res) {
          _this.fullscreenLoading = false;
          const data = res.data
          if(data.status){
            _this.userToken = 'Bearer ' + data.token;
            // 将用户token保存到vuex中
            _this.changeLogin({ Authorization: _this.userToken });
            //如果存在参数
            if(_this.$route.query.redirect) {
              let redirect = _this.$route.query.redirect;
              //则跳转至进入登录页前的路由
              _this.$router.replace(redirect);
            }else{
              _this.$router.replace({ path: '/' });
            }
          }else{
            _this.$notify.error({
              title: '错误',
              message: data.msg,
              showClose: false,
              duration: 2000
            });
          }

        }).catch(function (error) {
          console.log(error);
        })
      }
    }
  }



